<template>
  <div class="custom-select" @blur="open = false">
    <div class="selected tw-bg-transparent tw-py-2.5 tw-px-3 tw-rounded tw-cursor-pointer tw-text-sm tw-flex tw-justify-between tw-items-center" :class="{ open: open }" @click="open = !open">
      <span v-if="selected">{{ selected }}</span>
      <span v-else>{{ vplaceholder }}</span>
      <d-icon class="tw-transform tw-transition tw-duration-500 tw-ease-out tw-ml-2.5" :class="{ 'tw--rotate-180': open }" icon="ArrowDownBlack"></d-icon>
    </div>
    <div class="items tw-bg-white tw-rounded-md tw-overflow-hidden tw-absolute tw-min-w-full" :class="{ selectHide: !open }">
      <div
        class="tw-py-3 tw-px-4 tw-cursor-pointer"
        :class="{ hoverColor: selected === option }"
        v-for="(option, i) of options"
        :key="i"
        @click="
          selected = option;
          open = false;
          $emit('input', option);
        "
      >
        {{ option }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    options: {
      type: Array,
      required: true,
    },
    default: {
      type: String,
      required: false,
      default: null,
    },
    placeholder: {
      type: String,
      required: false,
      default: null,
    },
  },
  data() {
    return {
      selected: this.default ? this.default : "",
      vplaceholder: this.placeholder ? this.placeholder : "Select",
      open: false,
    };
  },
  mounted() {
    this.$emit("input", this.selected);
  },
};
</script>

<style lang="scss" scoped>
.custom-select {
  position: relative;
  text-align: left;
  outline: none;
}

.custom-select .selected {
  border: 1px solid #dfe2ea;
  user-select: none;
  span {
    overflow: hidden;
    white-space: nowrap;
  }
}
.custom-select .selected:hover {
  border-color: #d0d3da;
}
.custom-select .selected.open {
  border-color: #b2b3b4;
}

.custom-select .items {
    z-index: 1;
  border: 1px solid #eeeff1;
  box-shadow: 0px 7px 14px rgba(17, 49, 83, 0.1);
  top: calc(100% + 3px); 
  animation: growDown 500ms;
}

.custom-select .items div {
  user-select: none;
}
.hoverColor {
  background-color: #eeeff1;
}
.custom-select .items div:hover {
  background-color: #eeeff1;
}

.selectHide {
  display: none;
}
@keyframes growDown {
    0% {
       opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
</style>
