<template>
  <router-link :to="`/newsdetail/${id}`">
    <div class="tw-rounded tw-overflow-hidden" ref="newscard">
      <img class="tw-w-full tw-h-full tw-object-cover" :src="image" alt="NewsImage1" />
    </div>
    <div class="news__box__card">
      <h4 class="tw-mt-4 tw-mb-2 tw-font-medium tw-text-lg tw-leading-6">{{ title }}</h4>
      <p class="news__box__card-text tw-text-sm tw-mb-1">{{ text }}</p>
      <p class="tw-text-sm tw-text-spGray-400">#{{type}}</p>
    </div>
  </router-link>
</template>

<script>
export default {
    name: "NewsCard",
    props: ['image', 'title', 'text', 'type', 'id'],
    mounted(){
      this.$refs.newscard.style.height = this.$refs.newscard.offsetWidth * 184 / 306 + 'px'
    }
};
</script>

