    text: 'Tabassum qiling <br> Suhbat qanchalik jiddiy bo‘lmasin, hech bo‘lmaganda tabassum mos keladigan bir necha daqiqalar bo‘ladi — uyalmang. Albatta, siz doimo jilmayishingizga hojat yo‘q, lekin bir necha bor samimiy tabassum qilishingiz mumkin va hatto zarur.Ishga joylashmoqchi bo‘lgan joyingiz bilan tanishing <br>Suhbatdoshga uning kompaniyasi tarixini yoddan bilishingizni aytish shart emas. Ammo bu ma’lumot siz ushbu joy haqida biron bir narsani bilasizmi yoki yo‘qmi degan savollarga javob berishi mumkin. <br><br>O‘zingizga baland baho bermang<br>Albatta, diplom va yutuqlar haqida gapirish mumkin, lekin siz o‘zingizni daho sifatida tutishingiz kerak emas. Maqtanishlarni tinglagandan so‘ng, ehtimol rahbarlarda "Nega bunday ajoyib va aqlli odam o‘z kampaniyasini boshqarmay, ishchi sifatida ishga joylashishga intilmoqda?" degan mantiqiy savol tug‘ilishi mumkin.<br><br>2017-yilda germaniyalik hamda amerikalik tadqiqotchilar ishidan ma’no va rohat qidiradigan insonlar o‘rtasida so‘rovnoma o‘tkazgan. Tadqiqot natijasiga ko‘ra, ishidan ma’no qidiradiganlar ikkinchi guruh kishilariga nisbatan karyera qilishga ko‘proq qiziqishi ma’lum bo‘lgan. Aslida esa faoliyat davomida balansni topa bilish kerak. Ishga bo‘lgan qiziqish, xursandchilik va rohatlanishni his eta bilish juda muhim.<br><br><h3>Sizga nima kerakligini o‘zingizdan boshqa hech kim bilmaydi</h3>Karyera qilish bir-biridan farq qilishi mumkin. Kimdir bir joyda uzoq vaqt ishlashni, kimdir qisqa fursatda ish joyini o‘zgartirib turishni, yana kimdir bo‘lsa, oldindan ma’lum ishga erishish uchun boshqasi orqali ko‘tarilishni ma’qul biladi. Qaysi yo‘lni tanlash — bu sizning xohishingiz, faqat qayerda va qachon ishlashingizdan qat’i nazar ishingizni sevib bajaring. Bunda ikki tomonga foyda — sevimli ish bilan shug‘ullanib siz baxtiyorlikni, rahbariyat esa sizdek salohiyatli xodimi borligidan mamnuniyatni his qiladi.<br><br><h3>Me’yorni bilish</h3>Ishni yaxshi ko‘rgan yaxshi, lekin butun hayotni ish bilan o‘tkazish h